//@ts-nocheck

import React, { FC, useState } from "react";
import {
  Box,
  Button,
  Dialog,
  DialogTitle,
  Divider,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Stack,
} from "@mui/material";
import { useSnackbar } from "notistack";

interface Props {
  open: boolean;
  handleClose: () => void;
  handleSort: (sortOption: string) => void;
  sortOption: string;
  setSortOption: () => void;
}

const AddSortingDialog: FC<Props> = ({
  open,
  handleClose,
  handleSort,
  sortOption,
  setSortOption,
}) => {
  const { enqueueSnackbar } = useSnackbar();
  const [helperState, setHelperState] = useState();

  const handleSave = () => {
    handleSort(helperState);
    enqueueSnackbar("Sortowanie zastosowane pomylnie", { variant: "success" });
    handleClose();
  };

  return (
    <Dialog open={open} onClose={handleClose} maxWidth={"lg"}>
      <DialogTitle
        sx={{ display: "flex", fontWeight: 600, minWidth: "600px" }}
        color={"primary"}
      >
        {"Zastosuj reguy sortowania"}
      </DialogTitle>

      <Divider />

      <Stack spacing={2} sx={{ padding: "16px" }}>
        <FormControl fullWidth>
          <InputLabel>Sortowanie</InputLabel>
          <Select
            value={helperState}
            onChange={(e) => setHelperState(e.target.value as string)}
          >
            <MenuItem value={"Rosnco po dacie stworzenia"}>
              {"Rosnco po dacie stworzenia"}
            </MenuItem>
            <MenuItem value={"Malejco po dacie stworzenia"}>
              {"Malejco po dacie stworzenia"}
            </MenuItem>
            <MenuItem value={"Rosnco po ocenie grupy"}>
              {"Rosnco po ocenie grupy"}
            </MenuItem>
            <MenuItem value={"Malejco po ocenie grupy"}>
              {"Malejco po ocenie grupy"}
            </MenuItem>
            <MenuItem value={"Rosnco po iloci czonkw"}>
              {"Rosnco po iloci czonkw"}
            </MenuItem>
            <MenuItem value={"Malejco po iloci czonkw"}>
              {"Malejco po iloci czonkw"}
            </MenuItem>
          </Select>
        </FormControl>
        <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
          <Button variant={"contained"} onClick={handleSave}>
            Zapisz regu sortowania
          </Button>
        </Box>
      </Stack>
    </Dialog>
  );
};

export default AddSortingDialog;
